import * as React from 'react';

import {ROUTES} from "../../config";
import AniLink from "gatsby-plugin-transition-link/AniLink";
import {styles} from './wigets.module.scss'
export const Widgets = () => {
  return (
    <section className={styles} >

      <AniLink swipe direction="up" hex="#2000044"  entryOffset={80}  duration={0.3} to={ROUTES.homeBeauty}>
        live makeup
      </AniLink>

      <h2>  or</h2>


      <AniLink swipe direction="up" hex="#663399"  entryOffset={80}  duration={1} to={ROUTES.gallery}>
        OPEN GALLERY
      </AniLink>
    </section>
  );
};
